/**
 * 初始化镜头对象（蝌蚪和镜头不一定重合，在中心部分，镜头不动
 * aCanvas: 画布DOM
 * aContenxt: 画布2D画笔对象
 * x: 用户蝌蚪当前x
 * y: 用户蝌蚪当前y
 * **/
export default class Camera {
  constructor(aCanvas, aContext, x, y) {
    this.canvas = aCanvas;
    this.context = aContext;

    this.x = x;
    this.y = y;

    this.minZoom = 1.3;
    this.maxZoom = 1.8;
    this.zoom = this.minZoom;

    this.backgroundColor = Math.random() * 360;
  }

  setupContext() {
    var translateX = this.canvas.width / 2 - this.x * this.zoom;
    var translateY = this.canvas.height / 2 - this.y * this.zoom;

    // Reset transform matrix
    this.context.setTransform(1, 0, 0, 1, 0, 0);
    this.context.fillStyle = 'hsl(' + this.backgroundColor + ',50%,10%)';
    this.context.fillRect(0, 0, this.canvas.width, this.canvas.height);

    this.context.translate(translateX, translateY);
    this.context.scale(this.zoom, this.zoom);

    if (window.debug) {
      // 画边框，摄像机边界
      this.drawDebug();
    }
  }

  update(model) {
    this.backgroundColor += 0.08;
    this.backgroundColor = this.backgroundColor > 360 ? 0 : this.backgroundColor;

    var targetZoom = model.camera.maxZoom + ((model.camera.minZoom - model.camera.maxZoom) * Math.min(model.userTadpole.momentum, model.userTadpole.maxMomentum)) / model.userTadpole.maxMomentum;
    model.camera.zoom += (targetZoom - model.camera.zoom) / 60;

    var delta = {
      x: (model.userTadpole.x - model.camera.x) / 30,
      y: (model.userTadpole.y - model.camera.y) / 30,
    };

    if (Math.abs(delta.x) + Math.abs(delta.y) > 0.1) {
      model.camera.x += delta.x;
      model.camera.y += delta.y;

      for (var i = 0, len = model.waterParticles.length; i < len; i++) {
        var wp = model.waterParticles[i];
        wp.x -= (wp.z - 1) * delta.x;
        wp.y -= (wp.z - 1) * delta.y;
      }
    }
  }

  // 获取当前位置和当前缩放级别的边界坐标值，上下左右
  getBounds() {
    return [{ x: this.x - this.canvas.width / 2 / this.zoom, y: this.y - this.canvas.height / 2 / this.zoom }, { x: this.x + this.canvas.width / 2 / this.zoom, y: this.y + this.canvas.height / 2 / this.zoom }];
  }

  // 获取minZoom时的边界，即可视范围最大时的边界
  getOuterBounds() {
    return [{ x: this.x - this.canvas.width / 2 / this.minZoom, y: this.y - this.canvas.height / 2 / this.minZoom }, { x: this.x + this.canvas.width / 2 / this.minZoom, y: this.y + this.canvas.height / 2 / this.minZoom }];
  }

  // 获取maxZoom时的边界，即可视范围最小时的边界
  getInnerBounds() {
    return [{ x: this.x - this.canvas.width / 2 / this.maxZoom, y: this.y - this.canvas.height / 2 / this.maxZoom }, { x: this.x + this.canvas.width / 2 / this.maxZoom, y: this.y + this.canvas.height / 2 / this.maxZoom }];
  }

  startUILayer() {
    this.context.setTransform(1, 0, 0, 1, 0, 0);
  }

  debugBounds(bounds, text) {
    this.context.strokeStyle = '#fff';
    this.context.beginPath();
    this.context.moveTo(bounds[0].x, bounds[0].y);
    this.context.lineTo(bounds[0].x, bounds[1].y);
    this.context.lineTo(bounds[1].x, bounds[1].y);
    this.context.lineTo(bounds[1].x, bounds[0].y);
    this.context.closePath();
    this.context.stroke();
    this.context.fillText(text, bounds[0].x + 10, bounds[0].y + 10);
  }

  drawDebug() {
    this.debugBounds(this.getInnerBounds(), 'Maximum zoom camera bounds');
    this.debugBounds(this.getOuterBounds(), 'Minimum zoom camera bounds');
    this.debugBounds(this.getBounds(), 'Current zoom camera bounds');
  }
}
