// 绘制箭头 有用户在屏幕外时绘制一个小箭头
export default class Arrow {
  constructor(tadpole, camera) {
    this.x = 0;
    this.y = 0;

    this.tadpole = tadpole;
    this.camera = camera;

    this.angle = 0;
    this.distance = 10;

    this.opacity = 1;
  }

  // 更新箭头角度 根据蝌蚪的坐标和镜头的坐标
  update() {
    this.angle = Math.atan2(this.tadpole.y - this.camera.y, this.tadpole.x - this.camera.x);
  }

  draw(context, canvas) {
    const cameraBounds = this.camera.getBounds();

    if (this.tadpole.x < cameraBounds[0].x || this.tadpole.y < cameraBounds[0].y || this.tadpole.x > cameraBounds[1].x || this.tadpole.y > cameraBounds[1].y) {
      const size = 4;

      let arrowDistance = 100;

      const angle = this.angle;
      const w = canvas.width / 2 - 10;
      const h = canvas.height / 2 - 10;
      const aa = Math.atan(h / w);
      const ss = Math.cos(angle);
      const cc = Math.sin(angle);

      if (((Math.abs(angle) + aa) % Math.PI) / 2 < aa) {
        arrowDistance = w / Math.abs(ss);
      } else {
        arrowDistance = h / Math.abs(cc);
      }

      const x = canvas.width / 2 + Math.cos(this.angle) * arrowDistance;
      const y = canvas.height / 2 + Math.sin(this.angle) * arrowDistance;

      const point = this.calcPoint(x, y, this.angle, 2, size);
      const side1 = this.calcPoint(x, y, this.angle, 1.5, size);
      const side2 = this.calcPoint(x, y, this.angle, 0.5, size);

      // Draw arrow
      context.fillStyle = 'rgba(255,255,255,' + this.opacity + ')';
      context.beginPath();
      context.moveTo(point.x, point.y);
      context.lineTo(side1.x, side1.y);
      context.lineTo(side2.x, side2.y);
      context.closePath();
      context.fill();
    }
  }

  calcPoint(x, y, angle, angleMultiplier, length) {
    return {
      x: x + Math.cos(angle + Math.PI * angleMultiplier) * length,
      y: y + Math.sin(angle + Math.PI * angleMultiplier) * length,
    };
  }
}
