/**
 * socket服务
 *
 * 服务器推
 * welcome： 当前用户链接上时，服务器会推一次
 * update: 服务器实时推送所有蝌蚪的位置/动量等信息，用于客户端更新这些对象
 * message: 服务器推送所有蝌蚪发送的聊天信息
 * close: 别的用户退出聊天室
 * redirect： 服务器要求重定向到某个网址，估计是授权等信息不符合时会推
 *
 * 客户端推
 * sendUpdate： 向服务器发送自身蝌蚪的各种信息
 * sendMessage：用户发送聊天消息
 * authorize：发送用户token授权信息给服务器
 * **/
import Tadpole from './Tadpole';
import Arrow from './Arrow';
import Message from './Message';
import $ from 'jquery';

let model;
let webSocket;

export let hasConnection;
export default class WebSocketService {
  constructor(m, w) {
    webSocket = w;
    model = m;

    hasConnection = false; // 是否连接上
  }

  /**
   * handler-欢迎，连接上socket时，后端应该推送welcome消息
   * 返回值
   * id: 用户的ID
   * 如果缓存有用户的名字，send： name:名字， 带上了用户蝌蚪对象
   * **/
  welcomeHandler(data) {
    hasConnection = true;
    console.log('连接上');
    model.userTadpole.id = data.token;
    model.tadpoles[data.token] = model.tadpoles[-1];
    delete model.tadpoles[-1];
  }

  /**
   * 服务器推来了别的蝌蚪更新的信息
   * data: 蝌蚪对象
   * **/
  updateHandler(data) {
    let newtp = false;

    // 是否是一个新的用户，是就初始化一个对象
    if (!model.tadpoles[data.token]) {
      newtp = true;
      model.tadpoles[data.token] = new Tadpole(); // 初始化一个蝌蚪对象
      model.arrows[data.token] = new Arrow(model.tadpoles[data.token], model.camera); // 用户在屏幕外时显示小箭头
    }

    const tadpole = model.tadpoles[data.token];
    tadpole.name = data.name;
    // 如果是用户自己，则直接返回
    if (tadpole.id == model.userTadpole.id) {
      return;
    }

    if (newtp) {
      tadpole.x = data.x;
      tadpole.y = data.y;
    } else {
      tadpole.targetX = data.x;
      tadpole.targetY = data.y;
    }

    tadpole.angle = data.angle;
    tadpole.momentum = data.momentum;
    tadpole.sex = data.sex;
    tadpole.icon = data.icon;
    tadpole.timeSinceLastServerUpdate = 0;
  }

  /**
   * 服务器推送别人发的消息 普通消息
   * id: 发消息的用户ID
   * message: 消息
   * type: message
   * **/
  messageHandler(data) {
    var tadpole = model.tadpoles[data.token];
    if (!tadpole) {
      return;
    }
    tadpole.timeSinceLastServerUpdate = 0;
    tadpole.messages.push(new Message(data.message));
  }

  /**
   * 有用户退出时，服务器会推closed
   * id: 用户ID
   * type: colsed
   * **/
  closedHandler(data) {
    if (model.tadpoles[data.id]) {
      delete model.tadpoles[data.id];
      delete model.arrows[data.id];
    }
  }

  // 服务器推送了网页从定向的消息，直接跳转到该网址
  redirectHandler(data) {
    document.location = data.url;
  }

  // 收到别人的私密消息
  toHandler(data, callback) {
    var tadpole = model.tadpoles[data.token];
    if (!tadpole) {
      return;
    }
    callback(tadpole);
  }

  // socket收到对应的信息，调用对应的函数
  processMessage(data, callback) {
    // console.log('接收到消息：', data);
    var fn = this[data.type + 'Handler'];
    if (fn) {
      fn(data, callback);
    }
  }

  // socket关闭
  connectionClosed() {
    hasConnection = false;
    console.log('关闭了');
    $('#cant-connect').fadeIn(300);
  }

  // 发送自身蝌蚪更新的消息
  sendUpdate(tadpole) {
    var sendObj = {
      type: 'update',
      x: parseFloat(tadpole.x.toFixed(1)),
      y: parseFloat(tadpole.y.toFixed(1)),
      angle: parseFloat(tadpole.angle.toFixed(3)),
      momentum: parseFloat(tadpole.momentum.toFixed(3)),
      sex: tadpole.sex,
      token: tadpole.id,
    };

    if (tadpole.name) {
      sendObj['name'] = tadpole.name;
    }

    if (tadpole.icon) {
      sendObj['icon'] = tadpole.icon;
    }

    webSocket.send(JSON.stringify(sendObj));
  }

  // 封装的发送消息函数
  sendMessage(msg, user, tadpole) {
    // 消息体
    var sendObj = {
      type: 'message',
      token: user.id,
      message: msg,
    };
    // 私密信息
    if (tadpole) {
      sendObj.type = 'to';
      sendObj.to = tadpole.id;
    }

    webSocket.send(JSON.stringify(sendObj));
  }
}
