<template>
  <div :class="['hello-you-com',{'show':isShow}]">
    <ul ref="talkArea">
      <li class="default"
          v-if="!talkDatas.length">你们现在可以开始聊天了</li>
      <li v-for="(item,index) in talkDatas"
          :class="['one', {'me': item.type === 0}]"
          :key="index">
        <div class="headbox"><img :src="talkObj.otherHead || defaultHead" /></div>
        <div class="infobox">
          <div class="name">{{talkObj.otherName}}</div>
          <div class="words">{{item.w}}</div>
        </div>
      </li>
    </ul>
    <div class="close-btn"
         @touchend="onClose">关闭</div>
  </div>
</template>
<script>
import defaultHead from "@/assets/img/default.jpg";
export default {
  name: "hello-you",
  data() {
    return {
      defaultHead,
      /**
       * 缓存的所有消息
       * {
          otherId: 1, // 对方的ID
          otherName: "对方的昵称",
          otherHead: "",

          data: [{ type: 0, w: "balabalabal" }, { type: 1, w: "balabalabal" }]
        }
       *  **/
      allData: []
    };
  },
  props: {
    isShow: { type: Boolean, default: () => ({}) },
    otherId: { type: String, default: "" }, // 当前聊天的，对方的ID
    newTalk: { type: Object, default: null } // 有新的消息
  },
  computed: {
    // 当前对话的对象
    talkObj() {
      const obj = this.allData.find(item => item.otherId === this.otherId);
      return obj
        ? {
            otherId: obj.otherId,
            otherName: obj.otherName,
            otherHead: obj.otherHead
          }
        : {};
    },
    // 当前对象和用户的所有对话缓存内容
    talkDatas() {
      const talkObj = this.allData.find(item => item.otherId === this.otherId);
      return talkObj ? talkObj.messages : [];
    }
  },
  watch: {
    /**
     * newV:{otherId: 对方的ID，w: 说话的内容, type: 0我说给对方，1对方说给我, name: 对方的名字，head:对方的头像 }
     */
    newTalk(newV) {
      if (newV) {
        let otherObj = this.allData.find(item => item.otherId === newV.token); // 找到对应的消息体
        if (!otherObj) {
          otherObj = {
            otherId: newV.token,
            otherName: newV.name,
            otherHead: newV.head,
            messages: []
          };
          this.allData.push(otherObj);
        }
        otherObj.messages.push({ type: newV.type, w: newV.w });
        if (otherObj.messages.length > 50) {
          otherObj.messages.shift();
        }
        this.$refs.talkArea.scrollTo(
          0,
          this.$refs.talkArea.scrollHeight + "px"
        );
      }
    }
  },
  methods: {
    onClose() {
      this.$emit("onClose");
    }
  }
};
</script>
