      &.one {
        &.me {
          justify-content: flex-end;
          .headbox {
            order: 2;
            margin-right: 0;
            margin-left: 10px;
          }
          .infobox {
            order: 1;
            .name {
              text-align: right;
            }
            .words {
              &::before {
                left: auto;
                right: -10px;
                transform: rotate(180deg);
              }
            }
          }
        }
        .headbox {
          width: 30px;
          height: 30px;
          overflow: hidden;
          border-radius: 100%;
          margin-right: 10px;
          flex: none;
          img {
            display: block;
