            object-fit: cover;
          }
        }
        .infobox {
          position: relative;
          flex: none;
          max-width: calc(100% - 80px);
          box-sizing: border-box;
          .name {
            font-size: 12px;
            color: #666;
          }
          .words {
            position: relative;
            box-sizing: border-box;
            padding: 5px;
