import Model from '@/game/Model';
import Tadpole from '@/game/Tadpole';
import WaterParticle from '@/game/WaterParticle';
import Camera from '@/game/Camera';
import WebSocketServiceClass, { hasConnection } from '@/game/WebSocketService';

export let model = {}; // 总数据对象
export let canvas;
export let context;
export let webSocket;
export let webSocketService;

/**
 * model:{
 *  userTadpole{}, // 用户蝌蚪对象
 *  settings{}, // 基础配置
 *  tadpoles[], // 所有蝌蚪对象，包括用户蝌蚪对象
 *  waterParticles[], // 水中粒子
 *  camera{}, // 镜头对象
 *  arrows{}, // 所有小箭头对象
 * }
 * **/
/**
 * App:{
 *    keyNav: 键盘控制移动的距离
 *    messageQuota： 用户当前可发送消息的余量
 *  }
 * **/

const keys = {
  esc: 27,
  enter: 13,
  space: 32,
  up: 38,
  down: 40,
  left: 37,
  right: 39,
  ctrl: 17,
};
const mouse = { x: 0, y: 0, worldx: 0, worldy: 0, tadpole: null }; // 鼠标信息
export default class App {
  constructor(aSettings, aCanvas, socketCallBack, token) {
    this.keyNav = { x: 0, y: 0 }; // 键盘控制
    this.messageQuota = 5; // 用户当前可发送消息的余量
    canvas = aCanvas;
    context = canvas.getContext('2d');
    this.resizeCanvas();
    model = new Model();
    model.settings = aSettings;

    model.userTadpole = new Tadpole(); // 初始化一个蝌蚪,作为用户的蝌蚪
    model.userTadpole.id = -1; // 用户蝌蚪的ID=-1
    model.tadpoles[model.userTadpole.id] = model.userTadpole;

    // 初始化水中的粒子，全屏总共150个粒子，移动只是粒子在移动
    model.waterParticles = [];
    for (let i = 0; i < 150; i++) {
      model.waterParticles.push(new WaterParticle());
    }

    model.camera = new Camera(canvas, context, model.userTadpole.x, model.userTadpole.y);

    model.arrows = {};

    // 初始化socket
    webSocket = new WebSocket(model.settings.socketServer, token);
    webSocket.onopen = e => this.onSocketOpen(e);
    webSocket.onclose = e => this.onSocketClose(e);
    webSocket.onmessage = e => this.onSocketMessage(e, socketCallBack);

    webSocketService = new WebSocketServiceClass(model, webSocket);
  }

  // 总的更新操作
  update() {
    // 每50帧让messageQuota+1,即每50帧率，用户可发送的聊天+1，最多5，防止说话太快
    if (this.messageQuota < 5 && model.userTadpole.age % 50 == 0) {
      this.messageQuota++;
    }

    // 更新用户蝌蚪，键盘控制优先
    // if (this.keyNav.x != 0 || this.keyNav.y != 0) {
    //   model.userTadpole.userUpdate(model.tadpoles, model.userTadpole.x + this.keyNav.x, model.userTadpole.y + this.keyNav.y);
    // } else {
    //   var mvp = this.getMouseWorldPosition();
    //   mouse.worldx = mvp.x;
    //   mouse.worldy = mvp.y;
    //   model.userTadpole.userUpdate(model.tadpoles, mouse.worldx, mouse.worldy);
    // }
    // 不需要键盘控制
    var mvp = this.getMouseWorldPosition();
    mouse.worldx = mvp.x;
    mouse.worldy = mvp.y;
    model.userTadpole.userUpdate(model.tadpoles, mouse.worldx, mouse.worldy);

    // 每6帧检测一次，如果用户蝌蚪有更新且socket已链接，就发送一次用户蝌蚪信息给socket
    if (model.userTadpole.age % 6 == 0 && model.userTadpole.changed > 1 && hasConnection) {
      model.userTadpole.changed = 0;
      webSocketService.sendUpdate(model.userTadpole);
    }

    // 更新镜头
    model.camera.update(model);

    // 更新所有蝌蚪，包括用户蝌蚪，传mouse是为了判断是否hover
    for (let id in model.tadpoles) {
      model.tadpoles[id].update(mouse);
    }

    // 更新水粒子
    for (let i in model.waterParticles) {
      model.waterParticles[i].update(model.camera.getOuterBounds(), model.camera.zoom);
    }

    // 更新所有箭头对象
    for (let i in model.arrows) {
      var arrow = model.arrows[i];
      arrow.update();
    }
  }

  // 总的绘制操作
  draw() {
    // 调整canvas缩放比例等，为接下来的绘制做准备
    model.camera.setupContext();

    // 画水粒子
    for (let i in model.waterParticles) {
      model.waterParticles[i].draw(context);
    }

    // 画所有蝌蚪,包括用户蝌蚪
    for (let id in model.tadpoles) {
      model.tadpoles[id].draw(context);
    }

    // 启动UI层（重制canvas变幻矩阵）
    model.camera.startUILayer();

    // 画小箭头
    for (let i in model.arrows) {
      model.arrows[i].draw(context, canvas);
    }
  }

  // socket连上的回调
  onSocketOpen() {
    console.log('Socket opened!');
  }

  // socket断开的回调
  onSocketClose(e) {
    console.log('Socket closed!', e);
    webSocketService.connectionClosed();
  }

  // socket收到信息的回调
  onSocketMessage(e, socketCallBack) {
    try {
      var data = JSON.parse(e.data);
      webSocketService.processMessage(data, socketCallBack);
    } catch (e) {
      // console.log(e);
    }
  }

  // 用户发送聊天消息
  sendMessage(msg) {
    if (this.messageQuota > 0) {
      this.messageQuota--;
      webSocketService.sendMessage(msg, model.userTadpole);
    }
  }

  // 鼠标按下的回调
  mousedown(e) {
    mouse.clicking = true;
    // 如果鼠标移到了某个蝌蚪上(不包括自己) 且 左键点击， 则返回这个蝌蚪对象
    if (mouse.tadpole && mouse.tadpole.hover && mouse.tadpole.onclick(e)) {
      return mouse.tadpole;
    }
    // 否则就是普通的移动，把用户蝌蚪的动量设置为最大
    if (model.userTadpole && e.which == 1) {
      model.userTadpole.momentum = model.userTadpole.targetMomentum = model.userTadpole.maxMomentum;
    }
    return false;
  }

  // 鼠标抬起的回调，用户动量归零
  mouseup(e) {
    if (model.userTadpole && e.which == 1) {
      model.userTadpole.targetMomentum = 0;
    }
  }

  // 鼠标移动的回调
  mousemove(e) {
    mouse.x = e.clientX;
    mouse.y = e.clientY;
  }

  // 鼠标按下回调
  keydown(e) {
    if (e.keyCode == keys.up) {
      this.keyNav.y = -1;
      model.userTadpole.momentum = model.userTadpole.targetMomentum = model.userTadpole.maxMomentum;
      e.preventDefault();
    } else if (e.keyCode == keys.down) {
      this.keyNav.y = 1;
      model.userTadpole.momentum = model.userTadpole.targetMomentum = model.userTadpole.maxMomentum;
      e.preventDefault();
    } else if (e.keyCode == keys.left) {
      this.keyNav.x = -1;
      model.userTadpole.momentum = model.userTadpole.targetMomentum = model.userTadpole.maxMomentum;
      e.preventDefault();
    } else if (e.keyCode == keys.right) {
      this.keyNav.x = 1;
      model.userTadpole.momentum = model.userTadpole.targetMomentum = model.userTadpole.maxMomentum;
      e.preventDefault();
    }
  }

  // 鼠标抬起 释放动量
  keyup(e) {
    if (e.keyCode == keys.up || e.keyCode == keys.down) {
      this.keyNav.y = 0;
      if (this.keyNav.x == 0 && this.keyNav.y == 0) {
        model.userTadpole.targetMomentum = 0;
      }
      e.preventDefault();
    } else if (e.keyCode == keys.left || e.keyCode == keys.right) {
      this.keyNav.x = 0;
      if (this.keyNav.x == 0 && this.keyNav.y == 0) {
        model.userTadpole.targetMomentum = 0;
      }
      e.preventDefault();
    }
  }

  // 开始触屏
  touchstart(e) {
    mouse.clicking = true;

    var touch = e.changedTouches.item(0);
    if (touch) {
      mouse.x = touch.clientX;
      mouse.y = touch.clientY;
    }

    // 如果鼠标移到了某个蝌蚪上(不包括自己) 且 左键点击， 则返回这个蝌蚪对象
    if (mouse.tadpole && mouse.tadpole.hover) {
      return mouse.tadpole;
    }

    if (model.userTadpole) {
      model.userTadpole.momentum = model.userTadpole.targetMomentum = model.userTadpole.maxMomentum;
    }
    return false;
  }

  // 结束触屏
  touchend() {
    if (model.userTadpole) {
      model.userTadpole.targetMomentum = 0;
    }
  }

  // 手指移动
  touchmove(e) {
    var touch = e.changedTouches.item(0);
    if (touch) {
      mouse.x = touch.clientX;
      mouse.y = touch.clientY;
    }
  }

  // 重置canvas尺寸
  resize() {
    this.resizeCanvas();
  }

  // 获取页面鼠标位置，映射到canvas位置
  getMouseWorldPosition() {
    return {
      x: (mouse.x + (model.camera.x * model.camera.zoom - canvas.width / 2)) / model.camera.zoom,
      y: (mouse.y + (model.camera.y * model.camera.zoom - canvas.height / 2)) / model.camera.zoom,
    };
  }

  // 重置canvas尺寸
  resizeCanvas() {
    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;
  }
}
