export default class TadpoleTail {
  constructor(tadpole) {
    this.joints = [];
    this.tadpole = tadpole;
    this.jointSpacing = 1.4;
    this.animationRate = 0;

    for (let i = 0; i < 15; i++) {
      this.joints.push({
        x: 0,
        y: 0,
        angle: Math.PI * 2,
      });
    }
  }

  update() {
    this.animationRate += 0.2 + this.tadpole.momentum / 10;

    for (let i = 0, len = this.joints.length; i < len; i++) {
      const tailJoint = this.joints[i];
      const parentJoint = this.joints[i - 1] || this.tadpole;
      let anglediff = parentJoint.angle - tailJoint.angle;

      while (anglediff < -Math.PI) {
        anglediff += Math.PI * 2;
      }
      while (anglediff > Math.PI) {
        anglediff -= Math.PI * 2;
      }

      tailJoint.angle += (anglediff * (this.jointSpacing * 3 + Math.min(this.tadpole.momentum / 2, Math.PI * 1.8))) / 8;
      tailJoint.angle += Math.cos(this.animationRate - i / 3) * ((this.tadpole.momentum + 0.3) / 40);

      if (i == 0) {
        tailJoint.x = parentJoint.x + Math.cos(tailJoint.angle + Math.PI) * 5;
        tailJoint.y = parentJoint.y + Math.sin(tailJoint.angle + Math.PI) * 5;
      } else {
        tailJoint.x = parentJoint.x + Math.cos(tailJoint.angle + Math.PI) * this.jointSpacing;
        tailJoint.y = parentJoint.y + Math.sin(tailJoint.angle + Math.PI) * this.jointSpacing;
      }
    }
  }

  draw(context) {
    const path = [[], []];

    for (let i = 0, len = this.joints.length; i < len; i++) {
      const tailJoint = this.joints[i];

      const falloff = (this.joints.length - i) / this.joints.length;
      const jointSize = (this.tadpole.size - 1.8) * falloff;

      const x1 = tailJoint.x + Math.cos(tailJoint.angle + Math.PI * 1.5) * jointSize;
      const y1 = tailJoint.y + Math.sin(tailJoint.angle + Math.PI * 1.5) * jointSize;

      const x2 = tailJoint.x + Math.cos(tailJoint.angle + Math.PI / 2) * jointSize;
      const y2 = tailJoint.y + Math.sin(tailJoint.angle + Math.PI / 2) * jointSize;

      path[0].push({ x: x1, y: y1 });
      path[1].push({ x: x2, y: y2 });
    }

    for (let i = 0; i < path[0].length; i++) {
      context.lineTo(path[0][i].x, path[0][i].y);
    }
    path[1].reverse();
    for (let i = 0; i < path[1].length; i++) {
      context.lineTo(path[1][i].x, path[1][i].y);
    }
  }
}
