<template>
  <div :class="['menu-page',{'show': isShow}]">
    <div class="box">
      <div class="photo-box">
        <img :src="userInfo.headImgUrl || defaultHead">
      </div>
      <div :class="['form-one',{'error': userNameError}]">
        <div class="title">昵称</div>
        <div class="inputbox"><input maxlength="12"
                 class="myinput"
                 v-model="inputUserName"
                 placeholder="请输入昵称"
                 @blur="goToTop"
                 @input="onCheckName"></div>
        <div class="err">需要昵称</div>
      </div>
      <div :class="['form-one',{'error': userSexError}]">
        <div class="title">性别</div>
        <div class="inputbox">
          <a-radio-group @change="onCheckSex"
                         @blur="goToTop"
                         name="userSex"
                         v-model="inputUserSex">
            <a-radio :value="1">男生</a-radio>
            <a-radio :value="2">女生</a-radio>
          </a-radio-group>
        </div>
        <div class="err">请选择性别</div>
      </div>
      <div :class="['form-one',{'error': userPhoneError}]">
        <div class="title">手机号</div>
        <div class="inputbox"><input class="myinput"
                 placeholder="请输入手机"
                 maxlength="11"
                 v-model="inputUserPhone"
                 @blur="goToTop"
                 @input="onCheckPhone"></div>
        <div class="err">请填写正确的手机号</div>
      </div>

      <button type="button"
              class="submit-btn"
              @click="onFormSubmit">保存</button>

    </div>
  </div>
</template>
<script>
import defaultHead from "@/assets/img/default.jpg";
export default {
  name: "userinfo-com",
  data() {
    return {
      defaultHead,
      userNameError: false,
      userSexError: false,
      userPhoneError: false,
      inputUserName: "",
      inputUserSex: -1,
      inputUserPhone: ""
    };
  },
  props: {
    isShow: { type: Boolean, default: false },
    userInfo: { type: Object, default: () => ({}) }
  },
  watch: {
    userInfo(newV) {
      if (newV) {
        this.inputUserName = newV.nickname;
        this.inputUserSex = newV.sex;
        this.inputUserPhone = newV.userAddress.mobile;
      }
    }
  },
  methods: {
    goToTop() {
      document.body.scrollTop = 0;
    },
    onCheckName() {
      if (this.inputUserName.trim().length) {
        this.userNameError = false;
        return true;
      }
      this.userNameError = true;
      return false;
    },
    onCheckPhone() {
      if (/^1[3456789]\d{9}$/.test(this.inputUserPhone)) {
        this.userPhoneError = false;
        return true;
      }
      this.userPhoneError = true;
      return false;
    },
    onCheckSex() {
      if (this.inputUserSex > -1) {
        this.userSexError = false;
        return true;
      }
      this.userSexError = true;
      return false;
    },
    // 用户信息保存
    onFormSubmit(e) {
      e.preventDefault();
      if (this.onCheckName() & this.onCheckPhone() & this.onCheckSex()) {
        const fd = new FormData();
        fd.append("nickname", this.inputUserName);
        fd.append("sex", this.inputUserSex);
        fd.append("userAddress.mobile", this.inputUserPhone);
        this.$emit("onFormSubmit", fd);
      }
    }
  }
};
</script>
