<template>
  <div class="home-page"
       @touchend="onMyTouchend"
       @touchmove.stop="onMyTouchmove">
    <!-- 测试面板 -->
    <!-- <div class="test-box">
      <ul>
        <li v-for="(item,index) in testData"
            :key="index">
          {{item}}
        </li>
      </ul>
    </div> -->
    <!-- 主体画布 -->
    <canvas id="canvas"
            ref="rootcanvas"></canvas>
    <!-- 下方用户操作区 -->
    <div :class="['talk-box',{'ready': wxReady}]"
         @touchstart="onTalkStart">

      <span v-if="wxReady"
            class="iconfont iconmaikefeng"></span>
      <span v-else
            style="font-size: 12px">Loading</span>
    </div>
    <!-- 悬浮在中间的talk提示 -->
    <div :class="['talk-info',{'show':isTalkShow}]">
      <div class="talk-1">
        <div><span class="iconfont iconmaikefeng" /></div>
        <div>
          <div class="anime-div">
            <div></div>
          </div>
        </div>
      </div>
      <div class="talk-2">
        <div>{{talkTime}}s</div>
        <div :style="`color:${talkCancel ? '#c22' : '#ccc'}`">{{talkCancel?'松开手指，取消发送' : '手指上滑，取消发送'}}</div>
      </div>
    </div>
    <!-- 右上菜单按钮 -->
    <!-- <span class="menu-btn iconfont iconcaidan"
          v-show="!isHelloShow"
          :style="`${isMenuShow ? 'transform: scale(.9,.9)':'transform: scale(1,1)'}`"
          @touchend="isMenuShow = !isMenuShow"></span> -->
    <!-- 用户信息修改页 -->
    <UserInfo :isShow="isMenuShow"
              :userInfo="userInfo"
              @onFormSubmit="onFormSubmit" />
    <!-- 私密聊天页 -->
    <HelloYou :isShow="isHelloShow"
              :newTalk="newTalk"
              :otherId="otherNow && otherNow.id"
              @onClose="onHelloYouClose" />
    <!-- 发起私密聊天信息框 -->
    <a-modal title="私密聊天"
             class="simiModal"
             :visible="isSiMiModalShow"
             @ok="onSiMiSubmit"
             @cancel="isSiMiModalShow = false">
      <div class="body-box">
        <div>
          <img :src="otherTempNow.icon">
        </div>
        <div>
          是否向{{otherTempNow.name || `ID:${otherTempNow.id}用户`}}进行私密聊天？
        </div>
      </div>

    </a-modal>
  </div>
</template>

<script>
// @ is an alias to /src
import Config from "@/config";
import gameApp from "@/game/App";

import { model, webSocketService, hasConnection } from "@/game/App";
import { mapState, mapActions } from "vuex";
import HelloYou from "@/components/HelloYou";
import UserInfo from "@/components/UserInfo";
export default {
  name: "home-page",
  data() {
    return {
      testData: [],
      settings: {
        socketServer: Config.socketUrl
      },
      app: null,
      isTalkShow: false, // 是否正在录音
      talkTime: 0, // 录音的时长
      talkTimer: null, // 录音时长计时器
      fileList: [], // 头像上传临时对象
      talkCancel: false, // 用户是否取消发送
      wxReady: false, // 微信是否初始化完毕
      isMenuShow: false, // 菜单是否显示
      isHelloShow: false, // 私密聊天页显示
      otherTempNow: null, // 准备跟谁操作
      otherNow: null, // 正在跟谁聊天, obj对象
      isSiMiModalShow: false, // 私密聊天确认框是否显示
      newTalk: null // 最新的私密消息
    };
  },
  components: {
    HelloYou,
    UserInfo
  },
  computed: {
    ...mapState({
      userInfo: state => state.app.userInfo
    })
  },
  mounted() {
    const isUrl = this.initURL();
    if (!isUrl) {
      return;
    }
    this.initEveryThing();

    console.log("有吗：", this.userInfo);
  },
  watch: {
    userInfo(newV) {
      console.log("userInfo改变：", newV);
      if (model && model.userTadpole) {
        model.userTadpole.sex = newV.sex;
        model.userTadpole.name = newV.nickname;
        model.userTadpole.icon = newV.headImgUrl;
      }
    }
  },
  methods: {
    ...mapActions({
      getToken: "app/getToken",
      getWechartConfig: "app/getWechartConfig",
      getUserInfo: "app/getUserInfo",
      updateUserInfo: "app/updateUserInfo"
    }),

    // 重制URL ?#
    initURL() {
      const location = window.location;
      if (location.href.indexOf("?#") < 0) {
        const href = location.hash.split("?")[0].replace("#", "?#");
        location.replace(href);
        return false;
      }
      return true;
    },
    /** 初始化微信权限相关 **/
    async initEveryThing() {
      // 判断是否是从Auth过来的，而不是刷新页面进来的
      if (!window.location.host.includes("localhost")) {
        const isSessionAuthed = sessionStorage.getItem("isSessionAuthed");
        if (isSessionAuthed === "true") {
          sessionStorage.setItem("isSessionAuthed", false);
        } else {
          this.$router.replace("/"); // 去授权
          return;
        }
      }

      const msg = await this.getToken();
      console.log("0:", msg);
      if (!msg || msg.status !== 200 || !msg.data || !msg.data.success) {
        this.$message.error("获取Token失败");
        return;
      }
      this.initApp(msg.data.token);
      const res = await this.getUserInfo();
      console.log("1:", res);
      if (!res || res.status !== 200 || !res.data) {
        this.$message.error("用户信息获取失败");
      }

      const res2 = await this.getWechartConfig(`${window.location.origin}/?`);
      console.log("2:", res2);
      if (!res2 || res2.status !== 200 || !res2.data) {
        this.$message.error("微信初始化信息获取失败");
        return;
      }

      const res3 = await this.initWxConfig(res2.data);
      console.log("3:", res3);
      this.wxReady = res3;
      console.log("4:", res3);
    },

    /** 初始化游戏程序和socket **/
    initApp(token) {
      console.log("得到了token", token);
      this.app = new gameApp(
        this.settings,
        document.getElementById("canvas"),
        this.socketCallBack,
        token
      );

      window.addEventListener("resize", this.resize, false);
      const r = this.$refs.rootcanvas;

      r.addEventListener("touchstart", this.touchstart, false);
      r.addEventListener("touchend", this.touchend, false);
      r.addEventListener("touchcancel", this.touchend, false);
      r.addEventListener("touchmove", this.touchmove, false);

      document.body.onselectstart = function() {
        return false;
      };
      setInterval(() => this.runLoop(), 30);
    },

    /** 游戏循环 **/
    runLoop() {
      this.app.update();
      this.app.draw();
    },
    /** window大小改变 **/
    resize(e) {
      this.app.resize(e);
    },

    touchstart(e) {
      const res = this.app.touchstart(e);
      // 点选了某个蝌蚪 不能是自己 && res.id !== model.userTadpole.id
      console.log("点选一个蝌蚪：", res);
      if (res) {
        // 点选了自己
        if (res.id === model.userTadpole.id) {
          this.isMenuShow = true;
        } else {
          // 点选了别人
          this.otherTempNow = res;
          this.isSiMiModalShow = true;
        }
      }
    },
    touchend(e) {
      this.app.touchend(e);
    },
    touchcancel(e) {
      this.app.touchcancel(e);
    },
    touchmove(e) {
      this.app.touchmove(e);
    },
    keydown(e) {
      this.app.keydown(e);
    },
    keyup(e) {
      this.app.keyup(e);
    },

    //
    onUpBefore(file, fileList) {
      console.log("上传前：", file, fileList);
      if (
        !["image/jpeg", "image/png", "image/gif", "image/jpg"].includes(
          file.type
        )
      ) {
        this.$message.error("请上传jpeg/png/gif格式");
        return false;
      }
      if (file.size > 1024 * 500) {
        this.$message.error("头像应小于500kb");
        return false;
      }
    },
    onUpChange(info) {
      console.log("上传中", info);
      if (info.file.status !== "uploading") {
        console.log(info.file, info.fileList);
      }
      if (info.file.status === "done") {
        this.$message.success(`头像上传成功`);
        this.userPhoto = info.file.response.url;
      } else if (info.file.status === "error") {
        this.$message.error(`头像上传失败，请重试`);
      }
    },
    // 用户按下说话
    onTalkStart(e) {
      if (!this.wxReady) {
        return;
      }
      this.talkTime = 0;
      this.isTalkShow = true;
      this.mouseY = e.clientY || e.changedTouches[0].clientY;
      clearInterval(this.talkTimer);
      this.onTalkingTime();
      wx.startRecord({
        success: res => {
          this.testData.push(`开始录音:${res}`);
        }
      });
    },
    // 说话计时器
    onTalkingTime() {
      this.talkTimer = setTimeout(() => {
        if (this.talkTimer > 58) {
          // 说话>58秒，直接结束
          this.onMyTouchend();
          return;
        }
        this.talkTime = this.talkTime + 1;
        this.onTalkingTime();
      }, 1000);
    },

    // 用户鼠标抬起
    onMyTouchend() {
      clearInterval(this.talkTimer);
      if (!this.isTalkShow) {
        return;
      }
      if (this.talkCancel) {
        this.talkCancel = false;
        this.onStopRecord(false);
        return;
      }

      this.onStopRecord(true);
    },
    onMyTouchmove(e) {
      if (!this.isTalkShow) {
        return;
      }
      const clientY = e.clientY || e.changedTouches[0].clientY;
      if (Math.abs(clientY - this.mouseY) > 80) {
        // 取消录音
        this.talkCancel = true;
      }
    },

    // socket收到消息的回调
    socketCallBack(obj) {
      console.log("收到了私密信息", obj);
      // 如果当前用户不在私聊中，则可以发起一个通知
      if (!this.isHelloShow) {
        this.openNotification(obj);
      }
      // {otherId: 对方的ID，w: 说话的内容, type: 0我说给对方，1对方说给我 }
      this.newTalk = {
        otherId: obj.token,
        w: obj.message,
        type: 1
      };
    },

    /** 录音区域 **/
    // 录音结束 isUp是否上传
    onStopRecord(isUp) {
      this.isTalkShow = false;
      wx.stopRecord({
        success: res => {
          // window.alert("录音结束：" + String(isUp) + ",localId:" + res.localId);
          this.testData.push(`录音success:${JSON.stringify(res)}`);

          if (!isUp) {
            return;
          }
          if (!res.localId) {
            this.$message.error("您说得太快，没录上");
            return;
          }
          // 这里该执行转换录音为文字
          setTimeout(() => {
            this.soundToWords(res.localId);
          }, 200);
        },
        fail: res => {
          this.$message.error("录音失败，请重试");
        }
      });
    },
    // 音频转文字
    soundToWords(id) {
      const that = this;
      wx.translateVoice({
        localId: id, // 需要识别的音频的本地Id，由录音相关接口获得
        isShowProgressTips: 1, // 默认为1，显示进度提示
        success: res => {
          if (res.translateResult) {
            that.app.sendMessage(
              res.translateResult,
              model.userTadpole,
              this.isHelloShow ? this.otherNow : false
            );
          } else {
            this.$message.error("没听清您说的话...");
          }
          // window.alert("语音转换：" + res.translateResult);

          this.testData.push(`语音识别success:${JSON.stringify(res)}`);
          // alert(res.translateResult); // 语音识别的结果
        },
        fail: res => {
          this.$message.error("语音转换失败，请重试");
        }
      });
    },

    /**
     * 初始化微信JS-SDK
     * 用于微信公众号支付 和 微信H5支付
     * **/
    initWxConfig(data) {
      const me = this;
      console.log(
        "初始化微信：",
        data,
        data.appId,
        data.timestamp,
        data.nonceStr,
        data.signature
      );
      return new Promise((res, rej) => {
        if (typeof wx === "undefined") {
          res(false);
        }
        wx.config({
          debug: false,
          appId: data.appId,
          timestamp: data.timestamp,
          nonceStr: data.nonceStr,
          signature: data.signature,
          jsApiList: [
            "translateVoice",
            "startRecord",
            "stopRecord",
            "playVoice",
            "pauseVoice",
            "stopVoice",
            "onVoicePlayEnd",
            "uploadVoice",
            "downloadVoice"
          ]
        });
        wx.ready(() => {
          console.log("微信JS-SDK初始化READY");
          res(true);
        });
        wx.error(() => {
          console.log("微信JS-SDK初始化失败");
          res(false);
        });
      });
    },
    // 上传头像
    onUploadPhoto(info) {
      if (info.file.status !== "uploading") {
        // 上传中
      }
      if (info.file.status === "done") {
        this.$message.success(`头像上传成功`);
        model.userTadpole.icon = info.file.response.url;
      } else if (info.file.status === "error") {
        this.$message.error(`头像上传失败，请重试`);
      }
    },
    // 用户修改个人信息
    onFormSubmit(params) {
      this.updateUserInfo(params)
        .then(res => {
          if (res.status === 200 && res.data.success) {
            this.isMenuShow = false;
            this.$message.success("保存成功");
            this.getUserInfo();
          } else {
            this.$message.error("修改失败，请重试");
          }
        })
        .catch(() => {
          this.$message.error("修改失败，请重试");
        });
    },
    // 开始私密聊天 进入聊天界面
    onSiMiSubmit() {
      this.isHelloShow = true;
      this.isSiMiModalShow = false;
    },
    // 收到新的消息
    openNotification(obj) {
      const that = this;
      this.$notification.open({
        message: "私密消息",
        description: `${obj.name}的消息`,
        key: obj.id,
        duration: null,
        class: "simiMessage",
        placement: "topLeft",
        onClick: () => {
          that.otherNow = obj;
          that.isHelloShow = true;
          that.$notification.close(obj.id);
        }
      });
    },
    // 关闭私聊界面
    onHelloYouClose() {
      this.isHelloShow = false;
    }
  }
};
</script>

<style lang="less">
