  height: 100vh;
  width: 100vw;
  overflow: hidden;
  color: #f0f0f0;
  position: fixed;
  z-index: 1;
  .user-box {
    position: absolute;
    top: 20px;
    left: 20px;
    display: flex;
    .photo-box {
      width: 50px;
      height: 50px;
      border-radius: 4px;
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      .iconfont {
        font-size: 32px;
        color: #ccc;
      }
    }
    .info-box {
      margin-left: 20px;
      .name {
        font-weight: 600;
        color: #fcfcfc;
        font-size: 16px;
      }
      .sex {
        label {
          margin-right: 10px;
        }
      }
    }
  }
  .talk-box {
    position: absolute;
    bottom: 20px;
    left: 50%;
    margin-left: -30px;
    width: 60px;
    height: 60px;
    border-radius: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 20;
    color: #ccc;
    border: solid 2px #ccc;
    span {
      font-size: 32px;
    }
  }
  .talk-info {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 160px;
    height: 160px;
    border: solid 1px #fff;
    border-radius: 4px;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 20;
    margin: -80px 0 0 -80px;
    color: #ccc;
    padding: 10px;
    opacity: 0;
    pointer-events: none;
    transition: opacity 200ms;
    &.show {
      opacity: 1;
    }
    .talk-1 {
      display: flex;
      & > div {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        span {
          font-size: 36px;
        }
        .anime-div {
          width: 100%;
          height: 3px;
          border-radius: 2px;
          background-color: #222;
          position: relative;
          & > div {
            position: absolute;
            height: 100%;
            width: 0;
            background-color: rgb(5, 145, 98);
            border-radius: 2px;
            animation-name: talkanime;
            animation-duration: 1s;
            animation-iteration-count: infinite;
          }
        }
      }
    }
    .talk-2 {
      text-align: center;
      margin-top: 20px;
    }
  }
  // 菜单按钮
  .menu-btn {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 32px;
    z-index: 40;
    color: #f0f0f0;
    transition: transform 256ms;
  }
}

